import React, {Component} from "react"
import "./index.scss";
import {Head} from "../../components/head";
import {history} from "~";


class Sort extends Component{

    jxh=()=>{
        history.push("/sortshopping");
    }

    render(){

        
        return(
            <div>
                <Head title="分类" show="true"></Head>
                <div className="sortImg">
                    <img onClick={this.jxh} src="http://img.shop.wanmei.com/upload/ad/2017-07-20/dc1aa93a4db0447180686af65924ecf2.jpg" alt=""/>
                    <img src="http://img.shop.wanmei.com/upload/ad/2017-07-20/4e997b71bd5c437e97e80fbfb2141537.jpg" alt=""/>
                    <img src="http://img.shop.wanmei.com/upload/ad/2017-12-18/ade92b9d7e8f4c179b7a8919b13b886e.jpg" alt=""/>
                    <img src="http://img.shop.wanmei.com/upload/ad/2017-07-20/447c1d8bf94343299970ab26dc0f855f.jpg" alt=""/>
                    <img src="http://img.shop.wanmei.com/upload/ad/2017-07-20/56a56907bd22492bb970379a1e5ba83f.jpg" alt=""/>
                </div>
            </div>
        )
    }
}

export default  Sort